<template>
  <div class="container-gamenav">
    <div class="container-gamenav-style-left">
      <div class="top"></div>
      <div class="right"></div>
      <div class="bottom"></div>
      <div class="left"></div>
    </div>
    <div class="container-gamenav-style-right">
      <div class="min-top"></div>
      <div class="min-right"></div>
      <div class="min-bottom"></div>
    </div>
    <div class="container-gamenav-content">
      <ul class="container-gamenav-content-left">
       <li v-for="(ele,n) in some" :key="n" :class="{active:n == LeftActive}" @click="changeLeft(n,ele.navtext)">{{ele.navtext}}</li>
      </ul>
      <div class="container-gamenav-yidong-left" :class="{yidongActive:YidongAc}" @click="changeAc">
        {{YidongText}}
        <ul class="container-gamenav-yidong-left-text" v-if="YidongAc">
          <li v-for="(ele,n) in some" :key="n" @click="changeYidongText(ele.navtext)">{{ele.navtext}}</li>
        </ul>
      </div>
      <div class="container-gamenav-content-right" @click="changeShow" :class="{contentActive:RightStyle}">
       {{RightText}}
        <div class="container-gamenav-content-right-text" v-show="RightStyle">
          <span v-for="(ele,p) in some1" :key="p" @click="changeText(ele.navRight)">{{ele.navRight}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
data() {
  return {
    some:[
      {
        navtext:"定位不限"
      },
      {
        navtext:"刺客"
      },
      {
        navtext:"战士"
      },
      {
        navtext:"法师"
      },
      {
        navtext:"射手"
