    some1:[
      {navRight:"难度不限"},
      {navRight:"简单"},
      {navRight:"中等"},
      {navRight:"困难"},
    ],
    LeftActive:0,
    RightStyle:false,
    RightText:'难度不限',
    YidongText:'定位不限',
    YidongAc:false
  }
},
methods: {
  changeLeft(n,ele){
    this.LeftActive = n ;
    this.YidongText = ele;
    this.$emit('handlelocation',this.YidongText)
  },
  changeShow(){
    this.RightStyle = !this.RightStyle;
  },
  changeText(ele){
    this.RightText = ele;
    this.$emit('handledifficulty',this.RightText)
  },
  changeAc(){
    this.YidongAc = !this.YidongAc;
  },
  changeYidongText(ele){
    this.YidongText = ele;
    this.$emit('handlelocation',this.YidongText);
  }
},
}
</script>

