          this.some1 = this.some;
        }else{
          this.some1 = this.some.filter((ele)=>{
            return ele.difficulty == this.nandu
          })
        }
        
      } else {
        if (this.nandu == "难度不限") {
          this.some1 = this.some.filter((ele) => {
            return ele.location == this.dingwei;
          });
        } else {
          this.some1 = this.some
